<script>
import VueDraggableResizable from 'vue-draggable-resizable'
export default {
  name: 'DraggableTable',
  components: {
    // eslint-disable-next-line
    VueDraggableResizable
  },
  props: {
    mixins: { // 混合使用，排序和拖拽, 功能未实现
      type: Boolean,
      default: false
    },
    isDraggable: { // 拖拽排序
      type: Boolean,
      default: true
    },
    isWidth: { // 头部宽度
      type: Boolean,
      default: true
    },
    replaceFields: { // 重构字段
      type: Object,
      default: () => {
        return {
          key: 'businessId',
          dataIndex: 'dataIndex'
        }
      }
    },
  },
  data() {
    return {
      sourceObj: null, // 拖动的目标
      overObj: null, // 经过的元素
      targetObj: null, // 移动到的位置
      originalIndex: -1, // 缓存初始拖拽的索引值，如果为 -1 表示没有开始拖拽
      resizeableTitle: null,
      draggingState: {},
      columns: null,
      repFields: {
        key: 'businessId',
        dataIndex: 'dataIndex'
      },
      draggable: false // 是否可以拖拽
    }
  },
  mounted() {
    this.repFields = {
      ...this.repFields,
      ...this.replaceFields
    }
    this.draggable = this.columns.filter(item => item.scopedSlots && item.scopedSlots.customRender === 'customDrag').length ? false : this.isDraggable
    this.init()
  },
  methods: {
    init() {
      const { columns } = this.$attrs
      const draggingMap = {}
      columns.forEach(col => {
        draggingMap[col[this.repFields.dataIndex]] = col.width
      })
      this.draggingState = draggingMap
    },
    // 行样式
    rowClass(record, index) {
      if (this.sourceObj) {
        return this.sourceObj[this.repFields.key] === record[this.repFields.key] ? 'target-line' : 'target-process'
      }
      return ''
    },
    customRow(record, index) {
      const {on, style, ...options} = this.$attrs.customRow ? this.$attrs.customRow() : {}
      const styles = {
        ...{cursor: this.draggable ? 'pointer' : 'default' }, ...style}

      return {
        options,
        style: styles,
        on: {
          // 鼠标移入
          click: (event) => {
            this.$emit('row-click', {record, index})
          },
          dbclick: (event) => {
            this.$emit('row-dbClick', {record, index})
          },
          contextmenu: (event) => {
            this.$emit('row-contextmenu', {record, index})
          },
          mouseover: (event) => {
            // 兼容IE
            var ev = event || window.event
            ev.target.draggable = this.draggable
          },
          // 开始拖拽
          dragstart: (event) => {
            // 兼容IE
            var ev = event || window.event
            ev.stopPropagation()
            if (!this.draggable) return
            // 告知上级，拖拽开始，关闭排序
            this.$emit('draggableStart')
            // 阻止冒泡
            // 得到源目标数据
            this.sourceObj = record
          },
          // 拖动元素经过的元素
          dragover: (event) => {
            // 兼容 IE
            var ev = event || window.event
            // 阻止默认行为
            ev.preventDefault()
            if (!this.draggable) return
            if (!this.overObj) {
              this.overObj = record
            } else {
              if (this.overObj[this.repFields.key] !== record[this.repFields.key]) {
                this.overObj = record
                this.overReset(record)
              }
            }
          },
          // 鼠标松开
          drop: (event) => {
            // 兼容IE
            var ev = event || window.event
            // 阻止冒泡
            ev.stopPropagation()
            if (!this.draggable) return
            // 得到目标数据
            this.targetObj = record
            this.overReset(record, 'finish')
            this.result()
          }
        }
      }
    },
    overReset(record, status) {
      // 获得数据索引位置
      // const dataSource = this.$attrs['data-source'] || this.$attrs['dataSource']
      this.dataSource = this.$children[0].$children[0].getLocalData()
      // 获得拖拽对应索引
      const countIndex = () => {
        let index
        let baseIndex
        for (let i = 0; i < this.dataSource.length; i++) {
          if (this.dataSource[i][this.repFields.key] === record[this.repFields.key]) {
            index = i
          }
          if (this.dataSource[i][this.repFields.key] === this.sourceObj[this.repFields.key]) {
            baseIndex = i
          }
        }
        return {
          index,
          baseIndex
        }
      }
      const optIndex = countIndex()
      if (optIndex.index === optIndex.baseIndex) {
        if (status === 'finish') {
          this.$emit('draggable', {
            data: this.dataSource,
            source: this.sourceObj,
            ...optIndex,
            originalIndex: this.originalIndex
          }, 'finish')
        }
        return
      }
      // 缓存拖拽的初始索引
      if (this.originalIndex === -1) {
        this.originalIndex = optIndex.baseIndex
      }
      const splice = this.dataSource.splice(optIndex.baseIndex, 1)[0]
      this.dataSource.splice(optIndex.index, 0, splice)
      this.$emit('draggable', {
        data: this.dataSource,
        source: this.sourceObj,
        ...optIndex,
        originalIndex: this.originalIndex
      }, 'dragover')
    },
    result() {
      this.originalIndex = -1
      this.sourceObj = null
      this.targetObj = null
    },
    resetColumns() {
      // 重置表头数据
      this.columns = this.$attrs['columns']
    }
  },
  render(h, context) {
    const createElement = this.$createElement
    if (!this.columns) {
      this.columns = this.$attrs['columns']
    }
    const components = {
      header: {
        cell: (h, props, children) => {
          // 如果当前域下没有 columns 则赋值，防止当页面数据从外部刷新的时候导致宽度被还原
          if (!this.columns) {
            this.columns = this.$attrs['columns']
          }
          // 如果没有宽度但是有设置最小宽度
          let thDom = null // 拖拽宽度的值
          const { key, on, ...restProps } = props
          // 获得当前操作的列表头的数据
          let col = this.columns.find(col => {
            const k = col[this.repFields.dataIndex]
            return k === key
          })
          if (col && !col.width && col.minWidth) {
            col.width = col.minWidth
          }
          const title = col && col.slots && col.slots.title
          // 如果没有设置宽度，则直接返回表头
          if (!col || !col.width  || !this.isWidth) {
            return h('th', {
              ...props,
              ...restProps
            }, this.$slots[title] || children)
          }
          // 拖拽中……
          const onDrag = x => {
            // 验证最大宽度和最小宽度
            if (col.minWidth || col.maxWidth) {
              if ((col.minWidth && !col.maxWidth) || (!col.minWidth && col.maxWidth)) {
                if (col.minWidth && col.minWidth > x - 3) return
                if (col.maxWidth && col.maxWidth < x + 3) return
              }
            }
            this.draggingState[key] = 0
            col.width = Math.max(x, 1)
            this.$forceUpdate()
          }

          // 拖拽结束
          const onDragstop = () => {
            this.draggingState[key] = thDom.getBoundingClientRect().width
            this.$refs.tableDraggableHandle.left = thDom.getBoundingClientRect().width
          }

          return createElement('th', {
            ...props,
            ...restProps,
            class: `${props.class} resize-table-th`,
            key,
            directives: [
              {
                name: 'ant-ref',
                value: r => thDom = r
              }
            ],
            slot: 'customTitle',
            attrs: {
              width: col.width,
            },
            on: {
              click: ev => ev.stopPropagation()
            },
          }, [this.$slots[title] || children, createElement(VueDraggableResizable, {
            class: 'table-draggable-handle',
            ref: 'tableDraggableHandle',
            props: {
              key: col.key,
              w: 10,
              x: this.draggingState[key] || col.width,
              z: 1,
              axis: 'x',
              draggable: true,
              resizable: false
            },
            on: {
              dragging: onDrag,
              dragstop: onDragstop,
              click: ev => ev.stopPropagation()
            }
          })])
        }
      }
    }
    const dragIcon = () => {
      return this.$scopedSlots.customDrag ? this.$scopedSlots.customDrag() : h('div', {
        attrs: {
          style: 'display: inline-block;'
        }
      }, [h('a-icon', {
        props: {
          type: 'drag'
        }
      })])
    }
    const customDrag = () => {
      return createElement('div', {
        class: 'drag-icon',
        slot: 'customDrag',
        on: {
          mouseover: () => {
            this.draggable = true
          },
          mouseenter: () => {
            this.draggable = true
          },
          mouseout: () => {
            this.draggable = false
          }
        }
      }, [dragIcon()])
    }
    window.$that = this
    return createElement('a-table', {
      class: `table-draggable ${this.draggable ? 'table-draggable-ing' : ''}`,
      scopedSlots: {
        ...this.$scopedSlots,
        customDrag: customDrag
      },
      attrs: {
        ...this.$attrs,
      },
      slot: {
        ...this.$slots
      },
      props: {
        ref: 'table-draggable',
        ...this.$props,
        columns: this.columns,
        components,
        rowClassName: this.rowClass,
        customRow: this.customRow
      },
      on: {
        ...this.$listeners
      }
    })
  }
}
</script>

<style lang="less">
.table-draggable{
  &.table-draggable-ing{
    user-select: none;
  }
  .target-line{
    background:#e6f7ff;
  }
  .target-process:hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) > td{
    background:#e6f7ff !important;
  }
  .resize-table-th {
    position: relative;
    .table-draggable-handle {
      position: absolute;
      height: 100% !important;
      bottom: 0;
      left: -5px;
      cursor: col-resize;
      touch-action: none;
    }
  }
}
</style>
